.main {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: calc(100% - (4rem));
    margin-top: 200px;
  }
  
  .main__columnsWrapper {
    display: flex;
    flex-direction: row;
  }
  
  .main__columnsWrapper a {
    color: unset;
  }
  
  .main__columnLeft {
    display: flex;
    flex-direction: column;
    justify-content: top;
    width: 320px;
  }
  
  .main__columnLeft__text {
    line-height: 4rem;
    cursor: pointer;
  }
  
  .main__columnRight {
    display: flex;
    flex-direction: column;
  }
  
  .footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  
  .footer__spacer {
    flex-grow: 1;
  }
  
  .footer__btns_hidden {
    display: none;
  }
  
  .footer__btns {
    display: block;
  }
  
  .footer__btnsWrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }

  .logout_btn {
    background-color: transparent;
    color: #8585ad;
    border-style: none;
    cursor: pointer;
    text-align: left;
    
}